@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;500&display=swap');

body{
  font-family: 'Roboto', sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
}

.topLeft {
  display: flex;
  align-items: center; 
  gap: 8px;
  margin-left: 10px;
}

.websiteIcon {
  width: 60px;
}

.nameTopLeft {
  font-size: 18px;
  font-weight: 'bold';
  color: black;
  white-space: nowrap;
  text-decoration: none; 
}

.p1 {
  margin-top: 40px;
  display: flex;
  align-items: center;
  gap: 80px; 

  animation: fadeInAnimation ease 3s;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
}

.pic1 {
    margin-left: 50px;
    width: 400px;
    height: auto;
  }
  
.pic1 img {
    width: 100%;
    height: 100%;
    object-fit: contain; /* Keeps aspect ratio within the container */
  }

.intro {
    line-height: 1.9;
    width: 500px;
    height: 300px;
    margin-top: 20px;
    margin-right: 30px;
  }

  @keyframes fadeInAnimation {
    0% {opacity: 0;}
    100% {opacity: 1;}
  }

